/**
 *
 * @description serves to extend other correctors
 *
 */
export default class Corrector {
    constructor() {
        this.lang = "";
        this.numberRegex =
            /(^(?![a-zA-Z]))(((-?\d+(\.\d+)?((e)(\+|-)\d+)?i?)(?![a-zA-Z]))|0x([a-fA-F]|\d)+|0b(0|1)+|0o[0-7]+)(?=\b)/gi;
    }
    correct(text, index) {
        return [{ term: text, index: index }];
    }
}
